<template>
    <div class="accept">
        <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>车辆取车登记</span>    
        </div></el-col>
       </el-row>
       <div style="margin-left:30px;">
           <el-row :gutter="10">
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记时间：</td>
                   <td>
                    <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;" id="time"> 
                                <el-date-picker
                                   v-model="time"
                                   type="date"
                                    value-format="yyyyMMdd"
                                   placeholder="选择日期"  size="mini" style="width:150px;"  @blur="receive">
                               </el-date-picker>
                        </div>    
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记序号：</td>
                   <td><select name="" id="" v-model="receiveId"><option value="" disabled>请选择</option>
                       <option :value="item" v-for="item of list" :key="item.id">{{item.receiveId}}</option></select></td>      
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>取车人姓名：</td>
                   <td><input type="text" placeholder="请输入" v-model="taker"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>取车人证件类别：</td>
                   <td><select name="" id=""  v-model="takerType"><option value="" disabled>请选择</option><option :value="item.code" v-for="item of IdTypelist"  :key="item.id">{{item.typeName}}</option></select></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>取车人证件号：</td>
                   <td><input type="text" placeholder="请输入" @blur="reg" v-model="takerCode"><span style="color:red;font-size:12px;" v-show="red">输入的证件号码格式不正确</span></td>     
                 </tr>     
                </table>    
                </div></el-col>
               <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>取车人电话：</td>
                   <td><input type="text" placeholder="请输入" v-model="takerPhone" @blur="phone"><span style="color:red;font-size:12px;" v-show="tel">输入电话号码格式不正确</span></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>是否车主：</td>
                   <td><select name="" id=""  v-model="isself"><option value="" disabled>请选择</option><option value="1">是</option><option value="2">不是</option></select></td>     
                 </tr>     
                </table>    
                </div></el-col>
                </el-row>
       </div>
       <div >
           <el-row>
                <el-button type="primary" @click="define">确定</el-button>
                <el-button type="info" @click="reset">取消</el-button>
            </el-row>
       </div>
    </div>
</template>
<script>
import axios from "axios"
export default {
    data() {
        return {
            time:"",
            list:"",
            taker:"",
            takerCode:"",
            takerPhone:"",
            isself:"",
            receiveId:"",
            red:false,
            tel:false,
            IdTypelist:"",
            takerType:""
        }
    },
    created() {
       this.getIdType()
    },
    methods: {
      reset(){
            this.time=""
            this.taker=""
            this.takerCode=""
            this.takerPhone=""
            this.isself=""
            this.receiveId="",
            this.takerType=''
      },
      getIdType(){
        axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                     token:JSON.parse(localStorage.getItem('data')).token
                           },
                      url:process.env.API_HOST+'/qy/getIdType', 
                           
                  }).then(res=>{
                    console.log(res)
                    this.IdTypelist=res.data.data;
                  })
      },





        define(){
             if(
                this.time==""||
              this.taker==""||
            this.takerCode==""||
            this.takerPhone==""||
            this.isself==""||
            this.receiveId==""||this.red==true,
            this.tel==true||this.takerType==""
             ){
                 this.$message({
                                 message:'输入信息不完整',
                                  type: 'warning'
                            })
                return
             }else{
                  axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                     token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/RegistrationTake', 
                           data:JSON.stringify({
                           receiveId:this.receiveId,
                           taker:this.taker,
                           takerCode:this.takerCode,
                           takerPhone:this.takerPhone,
                           isself:this.isself
                           })
                  }).then((res)=>{
                      let that=this
                      switch(res.data.code){
                           case 200:
                            that.$message({
                                 message:'提交成功',
                                  type: 'success'
                            })
                            this.time=""
                            this.taker=""
                            this.takerCode=""
                            this.takerPhone=""
                            this.isself=""
                            this.receiveId="",
                            this.takerType=""
                            break;
                            case 404:
                                 that.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                            break;
                            case 505:
                                 that.$message({
                                 message:'token失效重新登录',
                                  type: 'warning'
                            })
                            that.$router.push("/login")
                            break;
                      }
                   })
                 .catch((err)=>{
                   console.log(err)
                      this.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                })
             }
        },
         phone(){
            if(!this.takerPhone.match(/^[1][3,4,5,6,7,8,9][0-9]{9}$/)){
                 this.tel=true
            }else{
              this.tel=false  
            }
        },
        reg(){
            if(!this.takerCode.match(/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/)){
                      this.red=true; 
            }else{
                this.red=false;
            }
        },
        receive(){
                     axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/getReceiveId', 
                          data:JSON.stringify({
                           startTime:this.time,
                           })
                  }).then((res)=>{
                      console.log(res)
                    switch(res.data.code){
                      case 200:
                      this.list=res.data.data
                      break;
                      case 505:
                        this.$router.push("/login")
                        break;
                      default:
                        this.$message({
                          message:"错误",
                          type:"error"
                        })  
                    }
                    //  this. receiveId=res.data.data
            })
        }
    },
}
</script>
<style lang="css" >
  .accept  .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
  .accept .grid-content{
      margin: 10px 0;
      margin-left: 30px;
  }
  .accept table tr td:nth-child(1){
     width: 140px;
     /* border: 1px solid red; */
     text-align: left;
  }
  .accept select{
      width:304px;
      height: 32px;
  }
  .accept input{
      width: 300px;
      height: 28px;
  }
 .accept .el-row{
     text-align: left;
     margin-left: 10%;
     margin-top: 20px;
 }
 .accept table tr td #time input.el-input__inner{
    border:0  !important;
    width: 200px;
} 
</style>


