<template>
    <div   id="detail">
         <el-row :gutter="10">
         <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>信息列表</span>    
        </div></el-col>
       </el-row>
       <el-table
      
       ref="singleTable"
        @current-change="handleCurrentChange"
       highlight-current-row
      :data="tableData"
      style="width: 90%;margin-top:20px;margin-left:5%;border-top: 1px solid #ccc;" size="small">
      <el-table-column
        prop="articleTitle"
        label="信息标题"
        width="450">
      </el-table-column>
      <el-table-column
        prop="articleType"
        label="信息分类"
         width="450">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间"
        width="450">
      </el-table-column>
  </el-table>
    

    <el-dialog  :visible.sync="dialogTableVisible">
  <el-row>
       <h1>{{data.articleTitle}}</h1>
       <div style="width:95%;height:2px;background:#ccc"></div>
        <div class="type-title" style="margin-top:30px;">
           <div class="left" style="float:left;margin-left: 30%;">
                 <span>消息类型：</span><span class="articleType" v-html="data.articleType"></span>
           </div>
           <div class="right" style="float:right;margin-right: 30%;">
                 <span>发布时间：</span><span class="createTime">{{data.createTime}}</span>
           </div>
           <div style="margin-top:50px;width:95%" v-html="data.articleContent">
            
           </div>
        </div>

   
    
      
        
  </el-row> 
</el-dialog>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data() {
    return {
      tableData:[],
      data:'',
      dialogTableVisible:false
    }
  },
  created() {
    this.getdata()
  },
    methods: {
      handleCurrentChange(value){
         if(value){
             this.data=value;
             this. dialogTableVisible=true
         }else{
           return
         }
      },
    
        getdata(){
          axios({
         url: process.env.API_HOST+'/qy/getIndexAbstract',
         method: "post",
          headers: {
          "Content-Type": "application/json;charset=UTF-8",
           token:JSON.parse(localStorage.getItem('data')).token
        }
      }).then(res=>{
        console.log(res)
        this.tableData=res.data.data;
      },err=>{
        console.log(err)
      })
        }
    },
}
</script>
<style >
     #detail .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   font-size: 24px;
   margin-bottom: 15px;
  }
    #detail div.cell{
    text-align: center;
   
}
    #detail td div.cell{
    text-align: center;
    cursor: pointer;
}
</style>


