<template>
    <div   id="comdetail">
         <el-row :gutter="10">
         <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>企业信息</span>    
            </div></el-col>
        </el-row>
        <div class="contain">
         <el-row>
            
          <v-input title="企业名称" :value="data.cname"></v-input>
          <v-input title="企业代码" :value="data.companyCode"></v-input>
          <v-input title="企业分类" :value="data.companyType"></v-input>
          <v-input title="行政区划" :value="data.register"></v-input>
          <v-input title="企业地址" :value="data.caddress"></v-input>
          <v-input title="联系电话" :value="data.contway"></v-input>
          <v-input title="传真" :value="data.fax"></v-input>
          <v-input title="邮箱" :value="data.email"></v-input>
          <v-input title="邮政编码" :value="data.postcode"></v-input>
          <v-input title="开业日期" :value="data.openDate"></v-input>
          <v-input title="经济类型" :value="data.ecotype"></v-input>
          <v-input title="法人代码" :value="data.legPersonCode"></v-input>
          <v-input title="法定代表人" :value="data.legPerson"></v-input>
          <v-input title="法人身份证号" :value="data.legPersonIdcode"></v-input>
          <v-input title="治安负责人" :value="data.defPrincipal"></v-input>
          <v-input title="保卫部门电话" :value="data.defDepartContway"></v-input>
          <v-input title="经营范围" :value="data.businessScope"></v-input>
          <v-input title="注册资本"  unit='万元（人民币）' :value="data.registeredCapital"></v-input>
          <v-input title="占地面积"  unit='平方米（㎡）' :value="data.floorSpace"></v-input>
          <v-input title="治安管理机构" :value="data.agency"></v-input>
          <v-input title="治安管理机构名称" :value="data.agencyName"></v-input>
          <v-input title="特行备案编号" :value="data.specRecordCode"></v-input>
          <v-input title="特行备案机构" :value="data.specRecordAgency"></v-input>
          <v-input title="特行备案机构名称" :value="data.specRecordAname"></v-input>
          <v-input title="行业许可证号" :value="data.induLicenceCode"></v-input>
          <v-input title="行业许可证号发证机构名称" :value="data.induLicenceAgency"></v-input>
          <v-input title="企业统一社会信用代码" :value="data.unisocialCreditCode"></v-input>
          <v-input title="统一社会信用代码发证机构名称" :value="data.unisocialLicenceAgency"></v-input>
          <v-input title="营业执照编号" :value="data.busLicenceCode"></v-input>
          <v-input title="营业执照发证机构名称" :value="data.busLicenceAgency"></v-input>
          <v-input title="税务登记证编号" :value="data.taxLicenceCode"></v-input>
          <v-input title="税务登记证发证机构名称" :value="data.taxLicenceAgency"></v-input>
          





         </el-row>
         </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            data:"",

        }
    },
    mounted() {
         this.getdata()
    },
    methods: {
        getdata(){
            axios({
             url: process.env.API_HOST+'/qy/showComInfo',
             method: "post",
            
             headers: {
            'Content-Type':'application/json;charset=UTF-8',
             token:JSON.parse(localStorage.getItem('data')).token
             },
              data: JSON.stringify({
                  companyCode:JSON.parse(localStorage.getItem('data')).companyCode
              })
             }).then(res=>{
                 console.log(res)
                    console.table(res.data.data)
                    this.data=res.data.data;
             })
        }
    },
}
</script>
<style >
  #comdetail .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   font-size: 24px;
   margin-bottom: 15px;
  }
   #comdetail div.cell{
    text-align: center
}
  
</style>


