<template>
    <div>
        <el-row :gutter="10" class="connectbody">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top connect">
           <span>车辆交接登记</span>    
        </div></el-col>
       </el-row>
       <div style="margin-left:30px;">
           <el-row :gutter="10" class="connectbody">
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记序号：</td>
                   <td><input type="text" placeholder="请获取" v-model="receiveId" readonly></td> 
                   <td><el-button type="primary" @click="get" size='small'>获取序列号</el-button></td>    
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>送车人姓名：</td>
                   <td><input type="text" placeholder="请输入" v-model="sender" ></td>     
                 </tr>     
                  <tr>
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>送车人电话：</td>
                   <td><input type="text" placeholder="请输入" v-model="senderPhone" @blur="phone"><span style="color:red;font-size:12px;" v-show="tel">输入电话号码格式不正确</span></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>送车人证件类别：</td>
                   <td><select name="" id="" v-model="idType"><option value="" disabled>请选择</option><option :value="item.code" v-for="item of IdTypelist" :key="item">{{item.typeName}}</option></select></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>送车人证件号：</td>
                   <td><input type="text" placeholder="请输入" v-model="senderCode" @blur="reg"><span style="color:red;font-size:12px;" v-show="red">输入的证件号码格式不正确</span> </td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td>取车时间：</td>
                   <td>
                        <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;" id="time"> 
                                <el-date-picker
                                   v-model="time"
                                   type="date"
                                   value-format="yyyy-MM-dd"
                                   placeholder="选择日期"  size="mini" style="width:150px;">
                               </el-date-picker>
                        </div>   
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content connect">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>是否车主：</td>
                   <td><select name=""  v-model="isselef" >
                       <option value="" disabled>请选择</option>
                       <option value="1">是</option>
                       <option value="2">否</option>
                       </select></td>     
                 </tr>     
                </table>    
                </div></el-col>
                </el-row>
       </div>
       <div >
           <el-row class="connectbody">
                <el-button type="primary" @click="success">确定</el-button>
                <el-button type="info"  @click="reset">取消</el-button>
            </el-row>
       </div>
    </div>
</template>
<script>
import axios from "axios"
export default {
    data() {
        return {
            time:"",
            receiveId:"",
            sender:"",
            senderCode:"",
            senderPhone:'',
            isselef:'',
            red:false,
            tel:false,
            txt:false,
            IdTypelist:"",
            idType:''
        }
    },
    created() {
        this.getIdType()
    },
    methods: {
        reset(){
           this.time=""
            this.receiveId=""
            this.sender=""
            this.senderCode=""
            this.senderPhone=''
            this.isselef='' 
            this.idType=""

        },
        reg(){
            if(!this.senderCode.match(/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/)){
                      this.red=true; 
            }else{
                this.red=false;
            }
        },
       
        phone(){
            if(!this.senderPhone.match(/^[1][3,4,5,6,7,8,9][0-9]{9}$/)){
                 this.tel=true
            }else{
              this.tel=false  
            }
        },
        get(){
            let that=this
              axios({
                 method: 'post',
                  url:process.env.API_HOST+ '/qy/RegistrationNumber',
                   headers: {
                      token:JSON.parse(localStorage.getItem('data')).token
                    },
              }).then((res)=>{
                  if(res.data.code==200){
                    that.receiveId=res.data.data
                  }else if(res.data.code==505){
                       that.$message({
                          message:'token失效重新登录',
                          type: 'warning'
                          });
                          setTimeout(() => {
                              that.$router.push('/login')
                          }, 100);
                  }else{
                      that.$message({
                          message:'获取失败',
                          type: 'error'
                          });
                  }
              })
        },
       
       
      getIdType(){
         axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/getIdType', 
                          }).then(res=>{
                              console.log(res)
                              this.IdTypelist=res.data.data;
                          })
      },
        success(){
            if( 
            this.receiveId==""||
            this.sender==""||
            this.senderCode==""||
            this.senderPhone==''||
            this.isselef==""||this.idType==''){
                 this.$message({
                                 message:'输入信息不完整',
                                  type: 'warning'
                            })
                return
            }else{
             axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/RegistrationHandover', 
                           data:JSON.stringify({
                           receiveId:this.receiveId,
                           sender:this.sender,
                           senderCode:this.senderCode,
                           senderPhone:this.senderPhone,
                           appointTime:this.time,
                           isself:this.isself
                           })
                  }).then((res)=>{
                      let that=this
                      switch(res.data.code){
                           case 200:
                            that.$message({
                                 message:'提交成功',
                                  type: 'success'
                            })
                            this.time=""
                            this.receiveId=""
                            this.sender=""
                            this.senderCode=""
                            this.senderPhone=''
                            this.isselef=''
                            break;
                            case 404:
                                 that.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                            break;
                            case 505:
                                 that.$message({
                                 message:'token失效重新登录',
                                  type: 'warning'
                            })
                            that.$router.push("/login")
                            break;
                      }
                   })
                 .catch((err)=>{
                   console.log(err)
                      this.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                })
        }
        }
    },
}
</script>
<style lang="css" scope>
    .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
  .connect{
      margin: 10px 0;
      margin-left: 30px;
  }
  table tr td:nth-child(1){
     width: 140px;
     /* border: 1px solid red; */
     text-align: left;
  }
  select{
      width:304px;
      height: 32px;
  }
  input{
      width: 300px;
      height: 28px;
  }
 .connectbody{
     text-align: left;
     margin-left: 10%;
     margin-top: 20px;
 }
 table tr td #time input.el-input__inner{
    border:0  !important;
} 
</style>


