// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import scroll from 'vue-seamless-scroll'
import echarts from 'echarts'
import axios from "axios"
import Print from 'vue-print-nb'
Vue.use(Print)



Vue.prototype.$axios = axios
Vue.prototype.$echarts = echarts
Vue.component('v-input', {
  
  props:["title",'value','unit'],
 
  template: ` <el-col :xs="24" :sm="24" :md="12" ><div >
  <table style="width:100%;">
       <tr>
             <td  style="width:30%;text-align:right"><span>{{title}}：</span></td>
             <td style="width:70%;text-align:left"><input type="text" style="width:394px;height:30px;"   v-bind:value="value" placeholder="请输入"  @input="$emit(\'input\', $event.target.valu)"   disabled="disabled"><span style="margin-left:10px">{{unit}}</span></td>
         </tr>
   </table>
 </div></el-col>`
})
Vue.component('v-in', {
  
  props:["title",'value','unit'],
 
  template: ` <el-col :xs="24" :sm="24" :md="12" ><div >
  <table style="width:100%;">
       <tr>
             <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>{{title}}：</span></td>
             <td style="width:70%;text-align:left"><input type="text" style="width:394px;height:30px;"   v-bind:value="value" placeholder="请输入"  @input="$emit(\'input\', $event.target.value)"><span style="margin-left:10px">{{unit}}</span></td>
         </tr>
   </table>
 </div></el-col>`
})

 Vue.use(scroll)
Vue.use(ElementUI)
Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



