<template>
<div id="box">
<ul id="con1" ref="con1" :class="{anim:animate==true}">
<li v-for="item in items" :key="item.id" @mouseenter="stopscoll" @mouseleave="startscoll" >
    <div style="width:100%;height:150px;overflow:hidden;text-align:left;"  class="">
              
              <span style="display:inline-block;width:150px;overflow:hidden">{{item.articleTitle}}</span><span style="margin-left:15%;display:inline-block;width:150px;">{{item.articleType}}</span><span style="margin-left:10%">{{item.createTime}}</span>
        
           
    </div>
  
    
    </li>
</ul>
</div>
</template>
<script>
import axios from 'axios'
export default {
data() {
return {
timer:null,
animate:false,
items:[
// {name:"中华人民共和国第一条"},
//           {name:"警察手册"},
//           {name:"法律法规注意事项"},
//           {name:"中华人民共和国第四条"},
//           {name:"劳动法00"},
//           {name:"中华人民共和国第六条"},
//           {name:"可持续型反转"},
]
}
},
created(){
this.getdata()
clearInterval(this.timer)
this.timer = null
this.setTimer()

},
destroyed: function () {
// 每次离开当前界面时，清除定时器
clearInterval(this.timer)
this.timer = null
},
methods: {
     getdata(){
          axios({
         url: process.env.API_HOST+'/qy/getIndexAbstract',
         method: "post",
          headers: {
          "Content-Type": "application/json;charset=UTF-8",
           token:JSON.parse(localStorage.getItem('data')).token
        }
      }).then(res=>{
        console.log(res)
        this.items=res.data.data;
      },err=>{
        console.log(err)
      })
        },
stopscoll(){
clearInterval(this.timer)
this.timer=null
},
startscoll(){
clearInterval(this.timer)
this.timer = null
this.setTimer()
},
setTimer(){
var that = this;
if(that.timer==null){
that.timer=setInterval(this.scroll,2000)
}
}, 
scroll(){
this.animate=true; // 因为在消息向上滚动的时候需要添加css3过渡动画，所以这里需要设置true
setTimeout(()=>{ // 这里直接使用了es6的箭头函数，省去了处理this指向偏移问题，代码也比之前简化了很多
this.items.push(this.items[0]); // 将数组的第一个元素添加到数组的
this.items.shift(); //删除数组的第一个元素
this.animate=false; // margin-top 为0 的时候取消过渡动画，实现无缝滚动
},500)
}
}
}
</script>
<style scoped>
*{
margin: 0 ;
padding: 0;
}
#box{
width:100%;
height:200px;
overflow: hidden;
padding-left: 30px;
}
.anim{
transition: all 0.5s;
margin-top: -50px;
}
#con1 li{
list-style: none;
/* line-height: 50px;  */
height: 50px;
width: 100%;
cursor: pointer;
color: #fff;

}
</style>



