<template>
    <div class="dubious">
        <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>可疑车辆附加登记</span>    
        </div></el-col>
       </el-row>
       <div style="margin-left:30px;">
           <el-row :gutter="10">
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记时间：</td>
                   <td>
                    <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:30px;" id="time"> 
                                <el-date-picker
                                   v-model="time"
                                   type="date"
                                   value-format="yyyyMMdd"
                                   placeholder="选择日期"  size="mini" style="width:150px;" @blur="receive">
                               </el-date-picker>
                        </div>    
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记序号：</td>
                    <td><select name="" id="" v-model="receiveId"><option value="" disabled>请选择</option>
                       <option :value="item" v-for="item of list" :key="item.id">{{item.receiveId}}</option></select></td>      
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>可疑情况类别：</td>
                   <td><select name="" id="" v-model="suspType"><option value="" disabled>请选择</option>
                    <option :value="item.typeCode" v-for="item of  suspTypeList" :key="item.id">{{item.typeName}}</option></select></td>     
                 </tr>     
                </table>    
                </div></el-col>
                  <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>疑点详细描述：</td>
                   <td><el-input
                        type="textarea"
                        autosize
                        placeholder="请输入"
                        v-model="suspDescribe">
                        </el-input></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>报告人：</td>
                   <td><input type="text" placeholder="请输入" v-model="reporter"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                </el-row>
       </div>
       <div >
           <el-row>
                <el-button type="primary" @click="define">确定</el-button>
                <el-button type="info" @click="reset">取消</el-button>
            </el-row>
       </div>
    </div>
</template>
<script>
import axios from "axios"
export default {
    data() {
        return {
            reporter:"",
            suspType:"",
            suspTypeList:"",
            list:"",
            receiveId:"",
            time:"",
            suspDescribe: ''
        }
    },
    mounted() {
         axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/getSuseType', 
                         }).then((res)=>{
                           this.suspTypeList=res.data.data
                         })
    },
    methods: {
      reset(){
         this.reporter=""
                            this.suspType=""
                             this.receiveId=""
                             this.time=""
                            this.suspDescribe=''
      },
        define(){
          if(
            this.reporter==""||
            this.suspType==""||
            this.receiveId==""||
            this.time==""||
            this.suspDescribe==''
          ){
            this.$message({
                                 message:'输入信息不完整',
                                  type: 'warning'
                            })
                return
          }else{
                axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                     token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/RegistrationSuspicious', 
                           data:JSON.stringify({
                           receiveId:this.receiveId,
                           reporter:this.reporter,
                            suspType:this.suspType,
                            suspDescribe:this.suspDescribe
                           })
                  }).then((res)=>{
                      let that=this
                      switch(res.data.code){
                           case 200:
                            that.$message({
                                 message:'提交成功',
                                  type: 'success'
                            })
                            this.reporter=""
                            this.suspType=""
                             this.receiveId=""
                             this.time=""
                            this.suspDescribe=''
                            break;
                            case 404:
                                 that.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                            break;
                            case 505:
                                 that.$message({
                                 message:'token失效重新登录',
                                  type: 'warning'
                            })
                            that.$router.push("/login")
                            break;
                      }
                   })
                 .catch((err)=>{
                   console.log(err)
                      this.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                })
          }
        },
        receive(){
                     axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url:process.env.API_HOST+ '/qy/getReceiveId', 
                          data:JSON.stringify({
                           startTime:this.time,
                           })
                  }).then((res)=>{
                      console.log(res)
                    switch(res.data.code){
                      case 200:
                      this.list=res.data.data
                      break;
                      case 505:
                        this.$router.push("/login")
                        break;
                      default:
                        this.$message({
                          message:"错误",
                          type:"error"
                        })  
                    }
            })
        }
    },
}
</script>
<style lang="css">
  .dubious  .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
  .dubious .grid-content{
      margin: 10px 0;
      margin-left: 30px;
  }
  .dubious table tr td:nth-child(1){
     width: 150px;
     /* border: 1px solid red; */
     text-align: left;
  }
  .dubious select{
      width:304px;
      height: 32px;
  }
  .dubious input{
      width: 300px;
      height: 28px;
  }
 .dubious .el-row{
     text-align: left;
     margin-left: 10%;
     margin-top: 20px;
 }
 .dubious table tr td #time input.el-input__inner{
    border:0  !important;
} 
.dubious textarea.el-textarea__inner{
     border-radius:0  !important;
     border:1px solid  #aaa;
     width:304px;
     height: 100px !important;;
}
</style>


