<template>
    <div class="index">
       <el-container>
         
          <el-header  :style="dl" >
            <div style="background:red">
              <div class="title"> 
                   <img src="@/assets/logo.png" alt=""  style="width:72px;height:72px;display:inline-block;  vertical-align: middle;margin-left:20px;">
                <span>汽修业管理系统</span></div>
         <ul>

           <li style="line-height:30px;text-align:center" @click="quit"><img src="@/assets/quit.png" alt=""  style="display:block;margin-left:2px;margin-top:20px"><span >退出</span></li>
           <li style="line-height:30px;text-align:center" @click="detail"><img src="@/assets/news.png" alt=""  style="display:block;margin-left:2px;margin-top:20px"><span >信息</span></li>
           <li @click="home" style="line-height:30px;text-align:center"><img src="@/assets/home.png" alt=""  style="display:block;margin-left:2px;margin-top:20px"><span >首页</span></li>
         </ul>
         </div>
  </el-header>
  <el-container >
    <el-aside  width="200px" style="background:#09407a">
         <div style="width:200px;height:200px;background:#326EC7;overflow:hidden">
              <div class="piclogo" style="overflow:hidden">
                 <img src="@/assets/comlogo.png" alt="" style="width:80px;height:80px;">
                </div>
              <p class="name" style="width:80%;margin:0 auto"><span style="color:#fff;font-size: 18px;">{{cname}}</span> </p>
              <p class="admin"  style="margin-top:25px;">管理员</p>
        </div>
        <el-menu
        :style="height"
      class="el-menu-vertical-demo"
      :unique-opened=true
      background-color="#09407a"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-submenu index="1">
        <template slot="title">
           <span style="margin-right:20%">
             <i class="el-icon-odometer"></i>
          <span>承接车辆</span>
            </span> 
        </template>
          <el-menu-item index="1-4-2" @click="connect"><span style="margin-left:20%" >车辆交接登记</span></el-menu-item>
          <el-menu-item index="1-4-1" @click="carinfo"><span style="margin-left:20%" >车辆信息登记</span> </el-menu-item>
          <el-menu-item index="1-4-3" @click="car"><span style="margin-left:20%" >车辆业务登记</span></el-menu-item> 
          <el-menu-item index="1-4-4" @click="accept"><span style="margin-left:20%" >车辆取车登记</span></el-menu-item> 
          
         
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
             <span style="margin-right:20%">
          <i class="el-icon-s-unfold"></i>
          <span>数据上报</span>
             </span>
        </template>
         
         <el-menu-item index="2-4-1" @click="dubious"><span style="margin-left:20%" >可疑车辆附加登记</span></el-menu-item>
          <el-menu-item index="2-4-2" @click="accident"><span style="margin-left:20%" >事故车辆附加登记</span></el-menu-item>
      </el-submenu>
      <el-submenu index="4">
        <template slot="title">
             <span style="margin-right:20%">
          <i class="el-icon-s-tools"></i>
          <span>系统管理</span>
             </span>
        </template>
         <el-menu-item index="4-4-2" @click="employee"><span >从业人员</span></el-menu-item>
          <el-menu-item index="4-4-1"><span style="margin-left:22%" @click="comdetail">企业信息管理</span></el-menu-item>
         
      </el-submenu>
    </el-menu>
    </el-aside>
    <el-container>
       <el-main  :style="height1">
            <router-view></router-view>
      </el-main> 
      <el-footer>Footer</el-footer>
    </el-container>
  </el-container>
</el-container> 
    </div>
</template>
<script>
export default {
    data(){
        return{
            cname:JSON.parse(localStorage.getItem('data')).cname,
            height:{
                width:"200px",
                height:"",
                background:"#09407a",
            },
            height1:{
              height:"",
              overflow: scroll,
              background:"#F2F6FD"
            } ,
             dl:{
              height:""
           }
        }
    },
    mounted() {
       
       this.mainheight();
       
    },
    created() {
      this.hh(),
      this.th()
       window.addEventListener('resize', this.hh);
    },
    destroyed() {
       window.removeEventListener('resize', this.hh)
    },
    methods:{
      employee(){
        this.$router.push("/employee")
      },
      detail(){
           this.$router.push("/detail")
      },
      comdetail(){
         this.$router.push("/comdetail")
      },
      quit(){
        localStorage.clear()
        this.$router.push("/login")
      },
      home(){
          this.$router.push("/home")
      },
        carinfo(){
          this.$router.push("/companyinfo")
        },
        connect(){
          this.$router.push("/connect")
        },
        car(){
          this.$router.push("/car")
        },
        accept(){
          this.$router.push("/accept")
        },
        accident(){
          this.$router.push("/accident")
        },
        dubious(){
          this.$router.push("/dubious")
        },
        hh(){
            this.height.height=window.innerHeight-290+'px';
        },
        mainheight(){
          this.height1.height=window.innerHeight-150+'px'
        },
        th(){
              this.dl.height='90px'
        },
        handleOpen(key, keyPath) {
         console.log(key, keyPath);
      },
        handleClose(key, keyPath) {
         console.log(key, keyPath);
      }
    },
}
</script>
<style  scope>
  .index .el-header, .el-footer {
    background-color: #163258;
    color: #333;
    /* text-align: center; */
     line-height: 90px;
     
  }
 .index .el-aside {
   
    color: #333;
    text-align: center;
    overflow: hidden;
  }
  .index .el-main {
    background-color: #fff;
    color: #333;
    text-align: center;
    position: relative;
    /* line-height: 160px; */
  }
  body > .el-container {
    margin-bottom: 40px;
  }
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
.index  el-menu-item{
      width: 200px;
  }
 .index header.el-header{
  font-size: 24px;
  font-family: 微软雅黑,serif;
    color: #f1f1f1;
   
}
 .index header.el-header  .title{
    float: left; 
    margin: 0;
    margin-left:5%;
     letter-spacing:5px;
}
 .index header.el-header ul li{
   float: right;
   margin-right: 50px;
  list-style:none;
  font-size: 18px;
  cursor: pointer;
}
 .index header.el-header ul{
  float: right;
  margin: 0;
}
   .index .piclogo{
      width:80px;
      height:80px;
     
      border-radius:50%;
      margin: 20px auto;
  }
  .index .piclogo img{
     width:80px;
      height:80px;
       border-radius:50%;
  }
  .index .name,.index .admin{
      height: 30px;
      margin: 0;
      color: #fff;
      border: 0;
      width: 200px;
      text-align: center;
  }
  .name>span{
    color: #fff;
    font-size: 18px;
  }
</style>



