<style scoped>
body{
   background: linear-gradient(41deg,#797de8,#6aa9ec)
}

.login{
  position: fixed;
  width: 100%;
  height: 100%;
  background: linear-gradient(41deg,#797de8,#6aa9ec)
}
 .login .Bg{
   position:absolute;
   left: 0;
   bottom: 0;
    
    
}
 .login .username,.password{
          width: 396px;
          height: 84px;
          border-bottom: 1px solid #8ab6fc;
          margin-left: 84px;
          line-height: 86px;
     }
    .login  .username{
         margin-top: 40px;
     }
   .login  .username img,.password img{
         width: 30px;
         height: 30px;
         vertical-align: middle
     }
   .login  .username input,.password input{
           width: 350px;
           height:40px;
           font-size: 18px;
           border: 0;
           outline: none;
           background: transparent ;
           color:#fff;
     }
  .login .loginbt{
         width: 240px;
         height: 54px;
        background:#fff;
         margin-left: 158px;
         font-size: 24px;
         margin-top:30px;
         color: #435E79;
         border-radius:30px;
         position: relative;
         z-index: 999;
        line-height: 54px;
        text-align: center;
        cursor: pointer;
     }
</style>
<template>
    <div class="login" :style="height">
      <el-row  >
        <el-col :span="8"><div class="grid-content " :style="height">
            <img src="@/assets/imgBg.png" alt=""  class="Bg">
        </div></el-col>
        <el-col :span="8">
            <div class="grid-content">
              <div style="margin-top:300px;">
                  <img src="@/assets/logo.png" alt="">
              </div>
              <div style="font-size:36px;text-align:center;margin-top:50px;color:#fff;">汽修业治安管理信息系统</div>
              <div style="font-size:16px;width:400px;margin: auto;white-space：normal;margin-top:10px;color:#fff;text-align:left;">Automobile repair industry public security management information system</div>
            </div></el-col>
              <el-col :span="8"><div class="grid-content bg-purple" style="height:404px;border-left:1px solid #8ab6fc;margin-top:336px;"  >
                    <div style="text-align:left;color:#fff;font-size:24px;margin-top:36px;margin-left:36px;">登录/Login</div>
                     <div class="username">
                        <img src="@/assets/user.png" alt="">
                        <input type="text" placeholder="请输入用户名" id="username"  v-model="username">
                    </div>
                     <div class="password">
                        <img src="@/assets/lock.png" alt="">
                        <input type="password" placeholder="请输入密码" id="password" v-model="password">
                    </div>
                    <div style="margin-top:10px;color:#fff">还没有账号？<a href=""  @click.prevent="regit" style="color:#fff;text-decoration:none;out-line:none">注册</a></div>
                     <div class="loginbt" @click="login" >
                        <span>登 录</span>
                    </div>
            </div></el-col>
      </el-row>
   
    </div>
</template>
<script>
import scoll from './scoll'
import axios from "axios"
export default {
    data() {
        return {
            username:"",
            password:"",
              height:{
                
                height:"",
               }
        }
    },
    created() {
        this.hh()
         window.addEventListener('resize', this.hh);
         var _self = this
 
    document.onkeydown = function(e) {
      var key = window.event.keyCode
 
      if (key === 13) {
        _self.login()
      }
    }

    },
     beforeDestroy() {
          window.removeEventListener('resize', this.hh)
    document.onkeydown = function(e) {
      var key = window.event.keyCode
 
      if (key === 13) {
 
      }
    }
  },

    methods: {
       
       
        regit(){
            this.$router.push("/register")
        },
        hh(){
            this.height.height=window.innerHeight+'px';
        },
        login(){
            let that =this;
            axios({
                 method: 'post',
                 url: process.env.API_HOST+'/qy/loginForPc',
                  headers: {
                     'Content-Type':'application/json;charset=UTF-8'
                         },
                 data:JSON.stringify({
                      username: that.username,
                      password: that.password,
                 })
                 }).then((res)=>{
                      localStorage.setItem("data",JSON.stringify(res.data.data))
                      
                      if(res.data.code==200){
                          that.$message({
                          message: '登录成功',
                          type: 'success'
                          });
                          that.$router.push("/index")
                      }else if(res.data.code==401){
                          that.$message({
                         message:'用户名或密码错误',
                         type: 'warning'
                          });
                      }else{
                           that.$message({
                          message:'登录失败',
                          type: 'error'
                          });
                      }
                   
                 }).catch(function (error) {
                    console.log(error);
                    that.$message({
                                message:'登录失败',
                                type: 'error'
                                });
        });
        }
    },
   components:{
       scoll
   }
}
</script>



