<template>
    <div class="car" >
          <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>车辆业务登记</span>    
        </div></el-col>
       </el-row>
       <div style=" background:#22508F; 
    width: 100%;
   border-radius:10px;">
    <div style="color:#fff;text-align:left;margin-left:3%;margin-top:10px;;font-size:24px;">添加业务</div>
           <el-row>
           <el-col  :md="24">
               <el-row :gutter="20">
                   <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content bg-purple topbar">
                        
                       <mainten></mainten>
                       </div></el-col>
                   <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content bg-purple topbar">
                       
                       <mt></mt>
                       </div></el-col>
                   <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content bg-purple topbar">
                      
                       <parts></parts>
                       </div></el-col>
                   <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content bg-purple topbar">
                      
                       <parking></parking>
                       </div></el-col>
               </el-row>
            </el-col>
            
      </el-row>
       </div>
    </div>
</template>
<script>
import mt from '@/views/mt'
import mainten from '@/views/mainten'
import parts from '@/views/parts'
import parking from '@/views/parking'
export default {
    components:{
        mt,mainten,parts,parking
    }
}
</script>
<style scope>
  .car  .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
.car .topbar{
     height: 80px;
     width: 300px;
     
     margin: 10px auto;
    
     
 }
 .car .topbar p{
     margin: 0;
     padding: 0;
     color: #fff;
     font-size: 24px;
 }
  /* .car .topbar button{
      background: red;
  } */
</style>


