<template>
    <div class="home">
        
              <el-row :gutter="10">
                <el-col :xs="24" :sm="12" :md="6" >
                    <div class="grid-content  data">
                       <div style="text-align:left;margin-top:30px;margin-left:20px;font-size:20px;color:#666">从业人员</div>
                       <div style="text-align:left;margin-top:10px;margin-left:20px;"><span style="font-size:36px;color:#6545d4">{{worker}}</span><span style="font-size:24px;color:#6545d4;margin-left:10px;">人</span></div>
                       <div style="position: absolute;top:22px; right:40px;">
                              <img src="@/assets/employee.png" alt="" >
                       </div>
                    </div>
                    </el-col>
                <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content data">
                        <div style="text-align:left;margin-top:30px;margin-left:20px;font-size:20px;color:#666">本日收车数</div>
                       <div style="text-align:left;margin-top:10px;margin-left:20px;"><span style="font-size:36px;color:#6545d4">{{accCar}}</span><span style="font-size:24px;color:#6545d4;margin-left:10px;">辆</span></div>
                       <div style="position: absolute;top:22px; right:40px;">
                              <img src="@/assets/carDel.png" alt="" >
                       </div>
                    </div></el-col>
                <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content data">
                       <div style="text-align:left;margin-top:30px;margin-left:20px;font-size:20px;color:#666">本日取车数</div>
                       <div style="text-align:left;margin-top:10px;margin-left:20px;"><span style="font-size:36px;color:#6545d4">{{takeCar}}</span><span style="font-size:24px;color:#6545d4;margin-left:10px;">辆</span></div>
                       <div style="position: absolute;top:22px; right:40px;">
                              <img src="@/assets/carPick.png" alt="" >
                       </div>
                    </div></el-col>
                <el-col :xs="24" :sm="12" :md="6" ><div class="grid-content data">
                        <div style="text-align:left;margin-top:30px;margin-left:20px;font-size:20px;color:#666">本日业务数</div>
                       <div style="text-align:left;margin-top:10px;margin-left:20px;"><span style="font-size:36px;color:#6545d4">{{basic}}</span><span style="font-size:24px;color:#6545d4;margin-left:10px;">个</span></div>
                       <div style="position: absolute;top:22px; right:40px;">
                              <img src="@/assets/busiNum.png" alt="" >
                       </div>
                    </div></el-col>
                </el-row>    
        
          <div class="contain">
               <el-row>
                    <el-col :xs="24" :sm="24" :md="14" >
                        <div style="width:100%;height:600px;background:#fff;margin-top:20px;border-radius:10px;box-shadow:5px 2px 7px #6545d4">
                            <div style="height:60px;width:100%;border-bottom:1px solid #7848E5 ;margin:0 auto;line-height:60px;text-align:left">
                                <!-- <span>业务增长趋势统计</span>
                                <span><span>7日</span><span>15日</span></span> -->
                                <span style="margin-left:10px;color:#666;font-size: 20px" class="charttitle">业务增长趋势统计</span>
                                <span style="display: inline-block;float:right;font-size:18px;margin-right:5px;color:#666; cursor: pointer;margin-right:26px;"><span style="margin-right:46px;"   @click="sve"  :class="{'class1':c}">7日</span><span  @click="tw"  :class="{class1:d}">15日</span></span>
                            </div>
                             <div id="chart" style="width:95%;height:500px;"></div>
                        </div>
                    </el-col>
                    <el-col :xs="24" :sm="24" :md="10">
                        <div style="width:90%;height:300px;border-radius:10px;margin-top:20px;background: linear-gradient(70deg,#3cc4e8,#7e48e5);overflow:hidden;position: relative;margin-left:5%;">
                             <div style="width:100%;height:60px;border-bottom:1px solid #ccc;margin:0 auto;line-height:60px;text-align:left;">
                                  <span style="margin-left:10px;color:#666;font-size: 20px;color:#fff">信息公告</span>
                             </div>
                            <div>
                                <scoll/>
                            </div>
                               <div style="position: absolute;width:100%;height: 50px;background:rgba(255,255,255,0.2);;bottom: 0;font-size: 18px;text-align: center;line-height:50px;"><a href="" style="color: #fff;text-decoration: none"   @click.prevent="detail">查看更多</a></div>
                        </div>
                        <div style="width:90%;height:280px;background:#fff;margin-top:15px;border-radius:10px;border:1px solid #ccc;margin-left:5%">
                            <div style="width:100%;height:60px;margin:0 auto;border-bottom:1px solid #ccc;line-height:60px;text-align:left">
                                 <span style="margin-left:10px;color:#666;font-size: 20px;color:#666;">每周业务统计</span>
                            </div>
                            <div id="total" style="width:85%;height:200px;margin-left:15%;margin-top:20px;"></div>
                        </div>
                    </el-col>
               </el-row>
           </div>       
    </div>
</template>
<script>
import axios from "axios"
// import scoll from "@/components/scoll"
import scoll from "@/components/scoll1"
export default {
    data() {
        return {
             timer:null,
            animate:false,
            accCar: "",
            basic: "",
            takeCar: '',
           worker: '',
           timeZone:'7',
           c:true,
           d:false,
        }
    },
    components:{
        scoll
    },
    mounted() {
        // this.drawLine(),
        this.drawtotal()
         

    },
    created() {
       this.getdata(),
       this.sve()
      
    },
    methods: {
      detail(){
             this.$router.push("/detail")
      },
      sve(){
          this.c=true,
          this.d=false,
            this.timeZone='7',
              axios({
                method: 'post',
              url: process.env.API_HOST+ '/qy/showBusinessData  ',
               headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                   token:JSON.parse(localStorage.getItem('data')).token
            },
             data:JSON.stringify({
                     timeZone:this.timeZone
                 })
         }).then(res=>{
           console.log(res)
           let day1=res.data.data.sevenDay.list;
           let day=day1.reverse() 
           var service=res.data.data.sevenDay.service;
           var beauty=res.data.data.sevenDay.beauty;
           var parts=res.data.data.sevenDay.parts;
           var park=res.data.data.sevenDay.park;
            let myChart = this.$echarts.init(document.getElementById('chart'))
            var option = {
          title : {
          text: '',
          subtext: ''
    },
    tooltip : {
        trigger: 'axis'
    },
    legend: {
        data:['维修','配件','美容','停放'],
        width:150,
        right:'right',
        textStyle:{
          color:'#666'
        }    },
    calculable : true,
    grid:{
        right:'15%'
    },
    xAxis : [ 
        {
            type : 'category',
            name:'日期（单位：日）',
           
            nameTextStyle:{
              width:1,
              fontSize:14
            },
            boundaryGap : false,
            // data : ['周一','周二','周三','周四','周五','周六','周日'],
            data:day,
            splitLine:{
                        show:false
                    },
             axisLine:{
               lineStyle:{
                 color:"#7848E5 "
               }
             }       
        }
    ],
    yAxis : [
        {
            type : 'value',
            name:"数量（单位：辆）",
            width:20,
            splitLine:{
                        show:false
                    },
                    axisLine:{
               lineStyle:{
                 color:"#7848E5 ",
               }
             }          
        }
    ],
    series : [
        {
            name:'维修',
            type:'line',
            smooth:true,
            left:30,
            symbol: 'circle',
            data:service,
            textStyle:{
              normal:{
                color:'red'
              }
            },
            itemStyle:{
              normal:{
                color:'#7375c7',
                lineStyle:{
                  color:'#7375c7'
                },
                areaStyle:{
                  color:'#7375c7'
                }
              }
            }
        },
        {
            name:'配件',
            type:'line',
            smooth:true,
            symbol: 'circle',
            itemStyle: {normal: {areaStyle: {type: 'default'}}},
            data:parts,
            itemStyle:{
              normal:{
                color:'#ae73b2',
                lineStyle:{
                  color:'#ae73b2'
                },
                areaStyle:{
                  color:'#ae73b2'
                }
              }
            }
          },
        {
            name:'美容',
            type:'line',
            smooth:true,
            symbol: 'circle',
            itemStyle: {normal: {areaStyle: {type: 'default'}}},
            data:beauty,
            itemStyle:{
              normal:{
                color:'#9bd2d7',
                lineStyle:{
                  color:'#9bd2d7'
                },
                areaStyle:{
                  color:'#9bd2d7'
                }
              }
            }
          },
        {
            name:'停放',
            type:'line',
            smooth:true,
            symbol: 'circle',
            itemStyle: {normal: {areaStyle: {type: 'default'}}},
            data:park,
            itemStyle:{
              normal:{
                color:'#c6c6c6',
                lineStyle:{
                  color:'#c6c6c6'
                },
                areaStyle:{
                  color:'#c6c6c6'
                }
              }
            }
          },
    ]
};
        myChart.setOption(option);
        })
      },
    tw(){
       this.d=true,
          this.c=false,
          this.timeZone='15',
           axios({
                method: 'post',
              url: process.env.API_HOST+ '/qy/showBusinessData  ',
               headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                   token:JSON.parse(localStorage.getItem('data')).token
            },
             data:JSON.stringify({
                     timeZone:this.timeZone
                 })
         }).then(res=>{
           console.log(res)
           let day1=res.data.data.fifteenDay.list;
           let day=day1.reverse() ;
           var service=res.data.data.fifteenDay.service;
           var beauty=res.data.data.fifteenDay.beauty;
           var parts=res.data.data.fifteenDay.parts;
           var park=res.data.data.fifteenDay.park;
            let myChart = this.$echarts.init(document.getElementById('chart'))
            var option = {
          title : {
          text: '',
          subtext: ''
    },
    tooltip : {
        trigger: 'axis'
    },
    legend: {
        data:['维修','配件','美容','停放'],
        width:150,
        right:'right',
        textStyle:{
          color:'#666'
        }    },
    calculable : true,
    grid:{
        right:'15%'
    },
    xAxis : [ 
        {
            type : 'category',
            name:'日期（单位：日）',
            nameTextStyle:{
              width:14,
              fontSize:14
            },
            boundaryGap : false,
            // data : ['周一','周二','周三','周四','周五','周六','周日'],
            data:day,
            splitLine:{
                        show:false
                    },
             axisLine:{
               lineStyle:{
                 color:"#7848E5 "
               }
             }       
        }
    ],
    yAxis : [
        {
            type : 'value',
            name:"数量（单位：辆）",
            width:20,
            splitLine:{
                        show:false
                    },
                    axisLine:{
               lineStyle:{
                 color:"#7848E5 ",
               }
             }          
        }
    ],
    series : [
        {
            name:'维修',
            type:'line',
            smooth:true,
            left:30,
            symbol: 'circle',
            data:service,
            textStyle:{
              normal:{
                color:'red'
              }
            },
            itemStyle:{
              normal:{
                color:'#7375c7',
                lineStyle:{
                  color:'#7375c7'
                },
                areaStyle:{
                  color:'#7375c7'
                }
              }
            }
        },
        {
            name:'配件',
            type:'line',
            smooth:true,
            symbol: 'circle',
            itemStyle: {normal: {areaStyle: {type: 'default'}}},
            data:parts,
            itemStyle:{
              normal:{
                color:'#ae73b2',
                lineStyle:{
                  color:'#ae73b2'
                },
                areaStyle:{
                  color:'#ae73b2'
                }
              }
            }
          },
        {
            name:'美容',
            type:'line',
            smooth:true,
            symbol: 'circle',
            itemStyle: {normal: {areaStyle: {type: 'default'}}},
            data:beauty,
            itemStyle:{
              normal:{
                color:'#9bd2d7',
                lineStyle:{
                  color:'#9bd2d7'
                },
                areaStyle:{
                  color:'#9bd2d7'
                }
              }
            }
          },
        {
            name:'停放',
            type:'line',
            smooth:true,
            symbol: 'circle',
            itemStyle: {normal: {areaStyle: {type: 'default'}}},
            data:park,
            itemStyle:{
              normal:{
                color:'#c6c6c6',
                lineStyle:{
                  color:'#c6c6c6'
                },
                areaStyle:{
                  color:'#c6c6c6'
                }
              }
            }
          },
    ]
};
        myChart.setOption(option,window.onresize = myChart.resize);
        })
      },
        getdata(){
              axios({
                method: 'post',
                url: process.env.API_HOST+ '/qy/showCountNum',
                headers: {
                  'Content-Type': 'application/json;charset=UTF-8',
                   token:JSON.parse(localStorage.getItem('data')).token
          },
        }).then(res=>{
          console.log(res)
          this.accCar=res.data.data.accCar,
          this.basic=res.data.data.basic,
            this.takeCar=res.data.data.takeCar,
           this.worker=res.data.data.worker
        })
        },
         BusinessData(){
              axios({
                method: 'post',
              url: process.env.API_HOST+ '/qy/showBusinessData  ',
          headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                   token:JSON.parse(localStorage.getItem('data')).token
            },
             data:JSON.stringify({
                     timeZone:this.timeZone
                 })
         }).then(res=>{
      console.log(res)
        })
       },
        drawtotal(){
            let myChart1 = this.$echarts.init(document.getElementById('total'))
             var option1 = {
            dataset: {
                source: [
                     [ 'amount', 'product'],
                     [ 0, '停放'],
                      [ 0, '配件'],
                      [ 0, '美容'],
                     [ 0, '维修'],
                     
                    
                     
                    // t,t1,t2,t3,t4
                ]
    },
    grid:{
      containLabel:true,
      x:0,
      y:-10,
      x2:40,
      y2:-10
    },
    xAxis: {name: 'amount', show:false,
                   axisTick:{
                    show:false
                  },axisLine:{
               lineStyle:{
                 color:"#000"
               }
             } ,
              },
    yAxis: {type: 'category', 
            axisTick: {
                show: false
            },
            axisLine:{
                          show:false,
               lineStyle:{
                 color:"#000"
               }
             },
             },
    series: [
        {
            type: 'bar',
            barWidth:20,
            encode: {
                x: 'amount',
                y: 'product'
            },
            label: {
          normal: {
            show: true,
            color:'#fff',
            position: 'inside',
        
        }
    },
            itemStyle:{
              normal:{
                color:function(params){
                  var colorList=['#8EE2F7','#4680E1','#8478FF',"#F98288"]
                  return colorList[params.dataIndex]
                }
              }
            }
        }
    ]
};
          myChart1.setOption(option1,window.onresize = myChart1.resize);   
        },

    },
}
</script>
<style >
    *{
        margin:0;
        padding: 0;
    }
    .home .data{
        position: relative;
        background: #fff;
        margin: 0 auto;
        width: 388px;
        height: 124px;
       border: 1px solid #ddd;
       border-radius: 5px;
       border-top:2px solid #7848E5 
    }
.class1{
  color: #6236E8;
}
</style>


