import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/index'
import Login from '@/components/login'
import Regit from '@/components/regit'
 import Home from '@/views/home1'
 import Companyinfo from '@/views/companyinfo'
 import Connect from '@/views/connect'
 import Car from '@/views/car'
 import Accept from '@/views/accept'
 import Accident from '@/views/accident'
 import Dubious from '@/views/dubious'
 import Carinfo from '@/views/carinfo'
 import Employee from '@/views/employee'
 import addEmployee from '@/views/addemployee'
 import Register from '@/views/register'
 import Updata from '@/views/updata'
 import detail from '@/views/detail'
 import comdetail from '@/views/comdetail'
 import updatainfo from '@/views/updatainfo'

Vue.use(Router)
var router= new Router({
  
  
  routes: [
    {
      path: '/',
      component: Login,
     redirect:"Login",
    },
   
    {
      path: '/register',
      component: Register,
     
    },
     {
       path: '/login',
       component:  Login
     },
     {
       path: '/regit',
       component:  Regit
     },
     {
       path: '/index',
       component: Index,
       meta: {
         requireAuth: true
     },
       redirect:"Home",
      children:[
         {
           path: '/home',
           component: Home,
           meta: {
            requireAuth: true
         },
         },
         {
           path: '/carinfo',
           component: Carinfo,
           meta: {
            requireAuth: true}
        },
         {
           path: '/detail',
           component: detail,
           meta: {
            requireAuth: true}
        },
         {
           path: '/comdetail',
           component: comdetail,
           meta: {
            requireAuth: true}
        },
         {
           path: '/companyinfo',
           component: Companyinfo,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/connect',
           component: Connect,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/car',
           component: Car,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/accept',
           component: Accept,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/accident',
          component: Accident,
          meta: {
            requireAuth: true
        },
         },
         {
           path: '/dubious',
           component: Dubious,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/employee',
           component: Employee,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/addemployee',
           component: addEmployee,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/updatainfo',
           component: updatainfo,
           meta: {
            requireAuth: true
        },
         },
         {
           path: '/updata',
           component: Updata,
           meta: {
            requireAuth: true
        },
         },
       ]
     }
  ]
})
 router.beforeEach((to, from, next) => {
   window.addEventListener("load",function(){
  //   console.log(to)
  //   console.log(from)
  //   next({
  //     path:'/index
  //   })
  
  
   })
  
   if (to.meta.requireAuth) {   //判断该路由是否需要登录权限
       if (JSON.parse(localStorage.getItem("data")).token) {   //通过vuex state获取当前的token是否存在
           next();
       }
       else{
           next({
               path: '/login',
               query: {redirect: to.fullPath}   //将跳转的路由path作为参数，登录成功后跳转到该路由
           })
       }
   }
   else {
       next();
   }
 })
 export default router;



