<template>
    <span style="margin:0 5px;">
        <el-button size="small"  @click="rule" >查看</el-button>
<el-dialog  :visible.sync="dialogTableVisible">
  <el-row>
    <el-col  :md="8" >
       <div style="width:150px;height:200px;border:1px solid #ccc;margin:0;float:right;overflow: hidden;">
          <img :src="url+data.photo" alt="" style="width:150px;height:200px;">
       </div>
    </el-col> 
    <el-col  :md="16"  >
       <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>员工编号：</td>
                   <td><input type="text"  :value="data.workerCode" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>员工姓名：</td>
                   <td><input type="text"  :value="data.name" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>性别：</td>
                   <td><input type="text" :value="data.sex" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col> 
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>联系电话：</td>
                   <td><input type="text"  :value="data.contway" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>现居地：</td>
                   <td><input type="text"  :value="data.nowAddress" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>学历：</td>
                   <td><input type="text"  :value="data.degree" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>身份证号：</td>
                   <td><input type="text"   :value="data.idcode" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>户口所在地：</td>
                   <td><input type="text"  :value="data.registerAddress" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>居住证号：</td>
                   <td><input type="text"  :value="data.residencePermit" readonly></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>入职时间：</td>
                   <td>
                    <input type="text" :value="data.entry" readonly>  
                    </td>     
                 </tr>     
                </table>    
                </div>
                </el-col> 
                
    </el-col>  
  </el-row> 
</el-dialog>
<!-- Form -->
    </span>
</template>
<script>
  export default {
    data() {
      return {
            name:"",
            data:"",
            contway:"",
            idcode:"",
            nowAddress:"",
            workerCode:"",
            takerPhone:"",
            sex:"",
            entry:"",
            url:"http://111.21.51.70:8089/jxy_data/image/",
           dialogTableVisible:false,
           formLabelWidth: '120px'
      }
    },
   
   methods: {
     rule(){
    
       if(this.data!=""){
         this.dialogTableVisible=true
       }else{
           this.$message({
                message: "请选择数据",
                type: "warning"
              })
          this.dialogTableVisible=false
          
       }
     }
   },
    props:["data"]
  };
</script>
<style  scoped>
  input{
      width: 400px;
      height: 28px;
      outline: none;
      border: 0;
  }
   table tr td:nth-child(1){
     width: 100px;
     /* border: 1px solid red; */
     text-align: left;
     padding: 0 0 0 40px;
     
  }
</style>


