<template>
    <div class="add">
        <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>从业人员信息修改</span>    
        </div></el-col>
       </el-row>
       <div style="margin-left:30px;">
          <el-row>
              <el-col :xs="24" :sm="24" :md="12">
                 
               
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>员工编号：</td>
                   <td><input type="text" placeholder="请输入" v-model="workerCode"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>员工姓名：</td>
                   <td><input type="text" placeholder="请输入" v-model="name"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>性别：</td>
                   <td><select name="" id=""  v-model="sex"><option value="" disabled>请选择</option><option value="1">男</option><option value="2">女</option></select></td>     
                 </tr>     
                </table>    
                </div></el-col> 
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>联系电话：</td>
                   <td><input type="text" placeholder="请输入" v-model="contway" @blur="phone"><span style="color:red;font-size:12px;" v-show="tel">请输入正确的手机格式</span></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>现居地：</td>
                   <td><input type="text" placeholder="请输入" v-model="nowAddress"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>学历：</td>
                   <td><input type="text" placeholder="请输入" v-model="degree"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>身份证号：</td>
                   <td><input type="text" placeholder="请输入" @blur="reg" v-model="idcode"><span style="color:red;font-size:12px;" v-show="red">输入的证件号码格式不正确</span></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>户口所在地：</td>
                   <td><input type="text" placeholder="请输入" v-model="registerAddress"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>居住证号：</td>
                   <td><input type="text" placeholder="请输入" v-model="residencePermit"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>入职时间：</td>
                   <td>
                    <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;" id="time"> 
                                <el-date-picker
                                   v-model="entry"
                                   type="date"
                                    value-format="yyyy-MM-dd"
                                   placeholder="选择日期"  size="mini" style="width:150px;"  >
                               </el-date-picker>
                        </div>    
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>离职时间：</td>
                   <td>
                    <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;" id="time"> 
                                <el-date-picker
                                   v-model="leave"
                                   type="date"
                                    value-format="yyyy-MM-dd"
                                   placeholder="选择日期"  size="mini" style="width:150px;"  >
                               </el-date-picker>
                        </div>    
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
              
               
               </el-col>   
              <el-col :xs="24" :sm="24" :md="12">
                  <el-upload
                    :on-change="onchange"
                    class="avatar-uploader"
                    :auto-upload="false"
                    :http-request="uploadFile"
                   
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
               </el-col>   
          </el-row>
       </div>
       <div style="margin-left:10%">
           <el-row>
                <el-button type="primary" @click="define">确定</el-button>
                <el-button type="info">取消</el-button>
            </el-row>
       </div>
    </div>
</template>
<script>

import axios from "axios"
export default {
    data() {
        return {
            time:"",
            list:"",
            leave:"",
            name:JSON.parse(localStorage.getItem('info')).name,
            contway:JSON.parse(localStorage.getItem('info')).contway,
            idcode:JSON.parse(localStorage.getItem('info')).idcode,
            nowAddress:JSON.parse(localStorage.getItem('info')).nowAddress,
            workerCode:JSON.parse(localStorage.getItem('info')).workerCode,
            workerId:JSON.parse(localStorage.getItem('info')).workerId,
            sex:"",
            entry:JSON.parse(localStorage.getItem('info')).entry,
            receiveId:"",
            red:false,
            tel:false,
            degree:JSON.parse(localStorage.getItem('info')).degree,
             imageUrl:"http://111.21.51.70:8089/jxy_data/image/"+JSON.parse(localStorage.getItem('info')).photo,
             registerAddress:JSON.parse(localStorage.getItem('info')).registerAddress,
             residencePermit:JSON.parse(localStorage.getItem('info')).residencePermit,
             file:[],
             
        }
    },
    mounted() {
      if(JSON.parse(localStorage.getItem('info')).sex==="女"){
        this.sex=2
      }else if(JSON.parse(localStorage.getItem('info')).sex==="男"){
        this.sex=1
      }else{
        this.sex=""
      }
    },
    methods: {
      
        onchange(file,fileList){
        var _this = this;
        
        var event = event || window.event;
        var file = event.target.files[0];
        
        var reader = new FileReader(); 
        //转base64
        reader.onload = function(e) {
        _this.imageUrl = e.target.result //将图片路径赋值给src
        }
        reader.readAsDataURL(file);
       
        _this.file = file;
        },

       
      beforeAvatarUpload(file) {
       
        const isLt2M = file.size / 1024 / 1024 < 2;

       
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
        define(){




            let formData = new FormData();
            formData.append("workerCode", this.workerCode);
            formData.append("name", this.name);
            formData.append("sex", this.sex);
            formData.append("contway", this.contway);
            formData.append("nowAddress", this.nowAddress);
            formData.append("degree", this.degree);
            formData.append("idcode", this.idcode);
            formData.append("registerAddress", this.registerAddress);
            formData.append("residencePermit", this.residencePermit);
            formData.append("entry", this.entry);
            formData.append("workerId", this.workerId);
             formData.append("file",this.file);
             formData.append("leave",this.leave);
           
             axios({
             url:process.env.API_HOST+ '/qy/changeWorkerInfo',
             method: "post",
             data: formData,
             headers: {
            "Content-Type": "multipart/form-data;charset=utf-8",
             token:JSON.parse(localStorage.getItem('data')).token
             }
             })
             .then((res) => {
             console.log(res)
             switch(res.data.code){
                      case 200:
                      this.$message({
                          message:"提交成功",
                          type:"success"
                        })  
                      
                      break;
                      case 505:
                           this.$message({
                          message:"token已经过期",
                          type:"warnin"
                        })  
                        var that = this
                        setTimeout(function(){that.$router.push("/login")},100)
                        
                        break;
                      default:
                        this.$message({
                          message:"提交失败",
                          type:"error"
                        })  
                    }
         }).catch((error)=>{
             console.log(error)
             this.$message({
                          message:"提交失败",
                          type:"error"
                        })  
         })

        },
            
         phone(){
            if(!this.contway.match(/^[1][3,4,5,7,8][0-9]{9}$/)){
                 this.tel=true
            }else{
              this.tel=false  
            }
        },
        reg(){
            if(!this.idcode.match(/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/)){
                      this.red=true; 
            }else{
                this.red=false;
            }
        },
        receive(){
           
                     axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/getReceiveId', 
                          data:JSON.stringify({
                           startTime:this.time,
                           })
                  }).then((res)=>{
                      console.log(res)
                    switch(res.data.code){
                      case 200:
                      this.list=res.data.data
                      
                      break;
                      case 505:
                        this.$router.push("/login")
                        break;
                      default:
                        this.$message({
                          message:"数据错误",
                          type:"error"
                        })  
                    }
                    
                    //  this. receiveId=res.data.data
            })
        }
    },
}
</script>
<style lang="css" >
  .add  .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
  .add .grid-content{
      margin: 10px 0;
      margin-left: 30px;
  }
  .add table tr td:nth-child(1){
     width: 140px;
     /* border: 1px solid red; */
     text-align: left;
  }
  .add select{
      width:304px;
      height: 32px;
  }
  .add input{
      width: 300px;
      height: 28px;
  }
 .add .el-row{
     text-align: left;
     margin-left: 5%;
     margin-top: 20px;
 }
 .add table tr td #time input.el-input__inner{
    border:0  !important;
    width: 200px;
} 
 .add .avatar-uploader .el-upload {
    border: 1px solid #d9d9d9;
    /* border-radius: 6px; */
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .add .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .add .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
 .add .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


