<template>
    <div class="employee" >
         <el-row :gutter="10">
         <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>从业人员</span>    
        </div></el-col>
       </el-row>
       <el-row :gutter="10">
       <el-col :span="8" :offset="16"  :xs="24"><div class="grid-content ">
            </div></el-col>
            <el-col :md="8" class="btn">
                <el-button size="small" @click="refresh">刷新</el-button >
                <el-button size="small" @click="add">新建</el-button >
                <el-button size="small" @click="updata">修改</el-button>
               
                

                <info :data="currentRow"></info>
                <!-- <el-button size="small" style="color:red;border:1px solid red">删除</el-button> -->
            </el-col>
</el-row> 
  <el-table
      
       ref="singleTable"
        @current-change="handleCurrentChange"
       highlight-current-row
      :data="tableData"
      style="width: 90%;margin-top:20px;margin-left:5%;border-top: 1px solid #ccc;" size="small">
      <el-table-column
        prop="workerCode"
        label="员工编号"
        width="100">
      </el-table-column>
      <el-table-column
        prop="name"
        label="员工姓名"
        width="80">
      </el-table-column>
      <el-table-column
        prop="sex"
        label="性别">
      </el-table-column>
      <el-table-column
        prop="contway"
        label="联系电话">
      </el-table-column>
      <el-table-column
        prop="nowAddress"
        label="现居住地址">
      </el-table-column>
      <el-table-column
        prop="degree"
        label="学历">
      </el-table-column>
      <el-table-column
        prop="idcode"
        label="身份证号">
      </el-table-column>
      <el-table-column
        prop="registerAddress"
        label="户口所在地">
      </el-table-column>
      <el-table-column
        prop="residencePermit"
        label="居住证号">
      </el-table-column>
    </el-table>
      <div style="position:absolute;bottom:30px;left:30%;">
    <el-pagination
       @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="10"
      layout="total, prev, pager, next, jumper,slot"
      :total="total"  :small="true" :background="true" prev-text="上一页" next-text="下一页">
      <span style="margin:0 10px;color:#666;font-family:'Cursive';font-weight:normal">每页10条</span><span style="margin:0 10px;color:#666;font-weight:normal">第{{currentPage}}/{{pages}}页</span>
    </el-pagination>
    </div>
    </div>
</template>
<script>
import axios from "axios"
import info from "./eminfo"
export default {
    methods: {
      
       refresh(){
         this.getdata('1')

       },
      getdata(num){
        axios({
                method: 'post',
                headers: {
                    'Content-Type':'application/json;charset=UTF-8',
                    token:JSON.parse(localStorage.getItem('data')).token
                        },
                      url:process.env.API_HOST+ '/qy/showWorker',
                      data:JSON.stringify({
                           status:"",
                           "pageNum": num.toString(),
                           "pageSize":"10"

                           }) 
              }).then((res)=>{
                  console.log(res)
                 this.tableData=res.data.data.list;
                this.total=res.data.data.total;
                this.pages=res.data.data.pages;
                this.currentPage=res.data.data.currentPage;
                })
              .catch((err)=>{
                console.log(err)
            })
      },
      updata(){
         if(this.currentRow===""){
            this.$message({
                          message:"未选择数据",
                          type:"warning"
                        })
           return
         }else{
              localStorage.setItem("info",JSON.stringify(this.currentRow))
        this.$router.push('/updata')
         }
       
    
      },
      add(){
        this.$router.push('/addemployee')
      },
          handleSizeChange(val) {
         console.log(`每页 ${val} 条`);
       },
      
      handleCurrentChange(val) {
        
        this.currentRow = val;
        console.log(val)
         this.getdata(val)
      }
       },
       components:{
         info
       },
    created() {
      this.getdata(1)
    },
     data() {
      return {
        page1:'上一页',
        page2:'下一页',
        value1: '',
     
        currentRow: "",
        tableData:"",
        show:true,
        total:"",
         pages:"",
        currentPage:"1"
      
       }
     }
}
</script>
<style>
  .employee .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   font-size: 24px;
   margin-bottom: 15px;
  }
 
  .employee .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
.employee .btn button.el-button{
    color: #22508F;
    border: 1px solid #22508F;
}
.employee div.cell{
    text-align: center
}
.employee .el-table__body tr.current-row>td{
  background-color: #22508F;
  color: #ffff; 
}
.employee  .el-table tbody tr:hover>td { 
  background: #22508F;
   color: #ffff;
}
</style>



