<template>
    <div class="register">
         <div style="width:100%;height:90px;background:#435595;line-height:90px;position:fixed;z-index:999">
             <div style="text-align:left;">
                   <img src="@/assets/logo.png" alt=""  style="width:72px;height:72px;display:inline-block;  vertical-align: middle;margin-left:20px;"><span style="margin-left:20px;font-size:24px;color:#fff">汽修业企业管理</span><span style="float:right;color:#fff;margin-right:50px;">已有账号？<a href=""   style="text-decoration: none;color:#fff"   @click.prevent="login"> 请登录</a></span>
             </div>
         </div>
           <div class="contain" >
                   <div style="text-align:left;color:#4b4395;margin-left:30px;margin-top:30px;font-size:24px;">
                       新用户注册
                   </div>
                   <div style="width:95%;height:100px;margin:30px auto;border:1px solid #ccc;border-radius:5px;">
                        <div style="text-align:left;margin-top:20px;margin-left:10px;font-size:20px;color:#666">注册流程</div>
                        <div style="text-align:left">
                               <span style="margin-left:160px;">用户注册</span><span style="margin:0 10px;">></span><span style="display:inline-block;width:94px;height:40px;background:#5440cd;line-height:40px;text-align:center;color:#fff">填写资料</span><span style="margin:0 10px">></span><span style="display:inline-block;width:94px;height:40px;background:#5440cd;line-height:40px;text-align:center;color:#fff">等待审核</span><span style="margin:0 10px;">></span><span style="display:inline-block;width:94px;height:40px;background:#5440cd;line-height:40px;text-align:center;color:#fff">邮箱通知</span><span style="margin:0 10px;">></span><span>注册成功</span>
                        </div>
                   </div>
                     <div style="width:95%;border:1px solid #ccc;margin:15px auto;border-radius:5px;">
                              <div style="text-align:left;margin-top:20px;margin-left:10px;font-size:20;color:#666">用户基本信息</div>
                              <div style="margin-left:50px;color:#5440cd;text-align:left;margin-top:30px;">以下内容为必填项：</div>
                              <div style="margin-top:30px;overflow:hidden">
                                  <el-row :gutter="10">
                                         <el-col  :xs="24" :sm="24" :md="12">
                                            <table style="width:100%;">
                                              <tr>
                                                <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>行政区划：</span></td>
                                                    <td style="width:70%;text-align:left;">
                                                      <select name="" id=""  style="width:25%;height:36px"  @change="getXaRegister"  v-model="register">
                                                        <option value=""  disabled>请选择</option>
                                                        <option :value="item.districtCode" v-for="item of registerlist" :key="item.id">{{item.districtName}}</option>
                                                        </select>
                                                      <select name="" id="" style="width:25%;height:36px;"    v-model="agency">
                                                        <option value="" disabled>请选择</option>
                                                        <option :value="item.agencyCode" v-for="item of getxaRegisterlist" :key="item.id">{{item.agencyName}}</option>
                                                        </select>
                                                    </td>
                                                </tr>
                                          </table>
                                        </el-col>
                                        <v-in title="企业名称"  v-model="cname"></v-in>
                                           <v-in title="经营范围" v-model="businessScope"></v-in>
                                        <v-in title="注册资本"   unit='万元（人民币）' type='Number' v-model="registeredCapital"></v-in>
                                         <v-in title="企业地址" v-model="caddress"></v-in>
                                          <v-in title="占地面积" unit='平方米（㎡）' type='Number' v-model="floorSpace"></v-in>
                                    <el-col :xs="24" :sm="24" :md="12" ><div >
                                           <table style="width:100%;">
                                              <tr>
                                                  <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>联系电话：</span></td>
                                                    <td style="width:70%;text-align:left"><input type="text"  placeholder="请输入手机格式"  style="width:394px;height:30px;" v-model="contway"   @blur="phone"><span   style="color:red;font-size:12px;"   v-show="p">请输入正确手机格式</span> </td>
                                                </tr>
                                          </table>
                                        </div></el-col>
                                          <v-in title="传真"  v-model="fax"></v-in>
                                             <v-in title="特行备案编码" v-model="specRecordCode"></v-in>
                                    <el-col :xs="24" :sm="24" :md="12" ><div >
                                         <table style="width:100%;">
                                              <tr>
                                                   <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>邮箱：</span></td>
                                                    <td style="width:70%;text-align:left"><input type="text"  placeholder="请输入邮箱格式"  style="width:394px;height:30px;" v-model="email"   @blur="cfox"><span   style="color:red;font-size:12px;"  v-show="f">请输入正确邮箱格式</span> </td>
                                                </tr>
                                          </table>
                                        </div></el-col> 
                                           <v-in title="特行备案机构" v-model="specRecordAgency"></v-in>
                                           <v-in title="特行备案机构名称" v-model="specRecordAname"></v-in>
                                          <v-in title="邮政编码" v-model="postcode"></v-in>
                                          <v-in title="行业许可证号"  v-model="induLicenceCode"></v-in>
                                        <v-in title="行业许可证发证机构" v-model="induLicenceAgency"></v-in>
                                    <el-col :xs="24" :sm="24" :md="12" ><div >
                                        <table style="width:100%;">
                                              <tr>
                                                  <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>经济类型：</span></td>
                                                    <td style="width:70%;text-align:left"><select name="" id=""  style="width:398px;height:36px;" v-model="ecotype"> <option value="" disabled>请选择</option>
                                                     <option :value="item.code" v-for="item of ecotypelist" :key="item.id">{{item.typeName}}</option></select></td>
                                                </tr>
                                          </table>
                                        </div></el-col>
                                        <v-in title="企业社会统一信用代码" v-model="unisocialCreditCode"></v-in>
                                        <v-in title="法人代码" v-model="legPersonCode"></v-in>
                                        <v-in title="企业社会统一信用代码发证机构" v-model="unisocialLicenceAgency"></v-in>
                                         <v-in title="法人代表" v-model="legPerson"></v-in>
                                        <v-in title="营业执照编号" v-model="busLicenceCode"></v-in>
                                     <el-col :xs="24" :sm="24" :md="12" ><div >
                                         <table style="width:100%;">
                                              <tr>
                                                <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>法人身份证号：</span></td>
                                                    <td style="width:70%;text-align:left"><input type="text"  placeholder="请输入证件格式"  style="width:394px;height:30px;" v-model="legPersonIdcode"  @blur="idcard"  re><span   style="color:red;font-size:12px;"   v-show="c">请输入正确证件格式</span> </td>
                                                </tr>
                                          </table>
                                        </div></el-col> 
                                        <v-in title="营业执照发证机构名称" v-model="busLicenceAgency"></v-in>
                                          <v-in title="治安负责人" v-model="defPrincipal"></v-in>
                                        <v-in title="税务登记证编号" v-model="taxLicenceCode"></v-in>
                                         <v-in title="保卫部门电话" v-model="defDepartContway"></v-in>
                                         <v-in title="税务登记证发证机构名称" v-model="taxLicenceAgency"></v-in>
                                       <el-col :xs="24" :sm="24" :md="12" ><div style="margin:0 auto;">
                                           <table style="width:100%;">
                                              <tr>
                                                  <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>治安管理机构：</span></td>
                                                    <td style="width:70%;text-align:left"><select name="" id=""  style="width:398px;height:36px;"  v-model="agency"  disabled><option value="">默认</option>
                                                    <option :value="item.agencyCode" v-for="item of getxaRegisterlist" :key="item.id">{{item.agencyName}}</option></select></td>
                                                </tr>
                                          </table>
                                        </div></el-col>
                                          <!-- <v-in title="治安管理机构名称" v-model="agencyName"></v-in> -->
                                   <el-col :xs="24" :sm="24" :md="12" ><div >
                                         <table style="width:100%;">
                                              <tr>
                                                  <td  style="width:30%;text-align:right"><span style="color:red">*</span><span>开业日期：</span></td>
                                                    <td style="width:70%;text-align:left">
                                                         <div style="width:396px;height:36px;border:1px solid #999;background:#FFF;line-height:32px;" id="time"> 
                                                        <el-date-picker
                                                        v-model="time"
                                                        type="date"
                                                            value-format="yyyyMMdd"
                                                        placeholder="选择日期"  size="mini" style="width:300px;" >
                                                    </el-date-picker>
                        </div>    
                                                    </td>
                                                </tr>
                                          </table>
                                        </div></el-col>
                                    </el-row>
                              </div>
                              <div style="margin-top:50px;margin-bottom:50px;">
                                  <el-row>
                                        <el-col :xs="24" :sm="24" :md="12" >
                                              <div >
                                                       <button style="width:242px;height:54px;border:0;border-radius:30px;color:#fff;background:#435595;font-size:24px;outline:none;  cursor: pointer;"  @click="define"> 提交 </button>
                                              </div>
                                        </el-col>
                                        <el-col :xs="24" :sm="24" :md="12" >
                                              <div>
                                                  <button :plain="true" style="width:242px;height:54px;border:0;border-radius:30px;color:#fff;background:#909399;font-size:24px;overflow:hidden;outline:none;  cursor: pointer;"   @click="reset">
                                                      取消
                                                  </button>
                                              </div>
                                        </el-col>
                                  </el-row>
                              </div>
                     </div>
           </div>
    </div>
</template>
<script>
 import axios from "axios"
export default {
    data() {
        return {
        time:'',
        registerCode:"",
        register:'',
        cname:"",
        caddress:"",
        contway:'',
        fax:"",
        email:"",
        postcode:"",
        ecotype:"",
        legPersonCode:"",
        legPerson:'',
        legPersonIdcode:'',
        defPrincipal:"",
        defDepartContway:"",
        businessScope:"",
        registeredCapital:"",
        floorSpace:'',
        agency:"",
        // agencyName: '',
        specRecordCode:'',
        specRecordAgency:"",
        specRecordAname:'',
        induLicenceCode:'',
        induLicenceAgency:'',
        unisocialCreditCode:'',
        unisocialLicenceAgency:'',
        busLicenceCode:"",
        busLicenceAgency:'',
        taxLicenceCode:'',
        taxLicenceAgency:'',
        ecotypelist:'',
        registerlist:'',
        getxaRegisterlist:'',
        agencyCode:"",
        p:false,
        f:false,
        c:false,
        massage:''
        }
    },
    created() {
        this.getEcotype(),
        this.getRegister()
    },
    methods: {
       open3() {
        this.$message({
          message: '警告哦，这是一条警告消息',
          type: 'warning',
        });
      },
      getXaRegister(){
        this.agency=""
           axios({
          method: 'post',
          url: process.env.API_HOST+'/qy/getXaRegister',
          headers: {
            'Content-Type':'application/json;charset=UTF-8'
          },
          data: JSON.stringify({
             districtCode:this.register
          })
           }).then(res=>{
            console.log(res)
             this.getxaRegisterlist=res.data.data;
           })
      },
        define(){
          if(
       this.time=='',
        this.register==""||
         this.cname== ""||
        this.caddress== ""||
        this.contway== ''||
        this.fax== ""||
        this.email== ""||
        this.postcode== ""||
        this.ecotype== ""||
        this.legPersonCode== ""||
        this.legPerson== ''||
        this.legPersonIdcode== ''||
        this.defPrincipal== ""||
        this.defDepartContway== ""||
        this.businessScope== ""||
        this.registeredCapital== ""||
        this.floorSpace== ''||
        this.agency== ''||
        // this.agencyName== ''||
        this.specRecordCode==''||
        this.specRecordAgency==""||
        this.specRecordAname==''||
        this.induLicenceCode== ''||
        this.induLicenceAgency== ''||
        this.unisocialCreditCode== ''||
        this.unisocialLicenceAgency== ''||
        this.busLicenceCode== ""||
        this.busLicenceAgency== ''||
        this.taxLicenceCode== ''||
        this.taxLicenceAgency== ''|| this.p==true || this.f==true || this.c==true
          ){
         console.log(this.time) 
        console.log( this.address)
         console.log( this.register)
        console.log( this.cname)
         console.log( this.caddress)
        console.log( this.contway)
        console.log( this.fax)
        console.log( this.email)
        console.log( this.postcode)
        console.log( this.ecotype)
        console.log( this.legPersonCode)
        console.log( this.legPerson)
        console.log( this.legPersonIdcode)
        console.log( this.legPersonIdcode)
        console.log( this.defPrincipal)
        console.log( this.defDepartContway)
        console.log( this.businessScope)
        console.log( this.registeredCapital)
        console.log( this.floorSpace)
        console.log( this.agency)
        console.log( this.specRecordCode)
        console.log( this.specRecordAgency)
        console.log( this.specRecordAname)
        console.log( this.induLicenceCode)
        console.log( this.induLicenceAgency)
        console.log( this.unisocialCreditCode)
        console.log( this.unisocialLicenceAgency)
        console.log( this.busLicenceCode)
        console.log( this.busLicenceAgency)
        console.log( this.taxLicenceCode)
        console.log( this.taxLicenceAgency)
             this.$message({
                                 message:'输入信息不完整或者格式不正确',
                                  type: 'warning'
                            })
                return
          }else{
            axios({
          method: 'post',
          url: process.env.API_HOST+'/qy/workReg',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          data: JSON.stringify({
             openDate: this.time,
            register: this.register,
            cname: this.cname,
            caddress: this.caddress,
            contway: this.contway,
            fax: this.fax,
            email: this.email,
            postcode: this.postcode,
            ecotype: this.ecotype,
            legPersonCode: this.legPersonCode,
            legPerson: this.legPerson,
            legPersonIdcode: this.legPersonIdcode,
            defPrincipal: this.defPrincipal,
            defDepartContway: this.defDepartContway,
            businessScope: this.businessScope,
            registeredCapital: this.registeredCapital,
            floorSpace: this.floorSpace,
            agency: this.agency,
            agencyName: this.agencyName,
            specRecordCode: this.specRecordCode,
            specRecordAgency: this.specRecordAgency,
            specRecordAname: this.specRecordAname,
            induLicenceCode: this.induLicenceCode,
            induLicenceAgency: this.induLicenceAgency,
            unisocialCreditCode: this.unisocialCreditCode,
            unisocialLicenceAgency: this.unisocialLicenceAgency,
            busLicenceCode: this.busLicenceCode,
            busLicenceAgency: this.busLicenceAgency,
            taxLicenceCode: this.taxLicenceCode,
            taxLicenceAgency: this.taxLicenceAgency
          })
        }).then((res) => {
          this.message=res.data.msg;
         switch(res.data.code){
              case 200:
               this.$message({
              message: this.message,
              type: 'success'
            }); 
             setTimeout(()=> {
               this.$router.push("/login")
            }, 1000)
            break;
            case 402:
               this.$message({
              message: this.message,
              type: 'error'
            }); 
            break;
             case 507:
               this.$message({
              message: this.message,
              type: 'warning'
            }); 
            break;
             case 508:
               this.$message({
              message: this.message,
              type: 'warning'
            }); 
            break;
            default:
               this.$message({
              message: '注册失败',
              type: 'error'})
         }
        }).catch((err) => {
          this.$message({
            message: '注册失败',
            type: 'error'
          });
        })
          }
        },
        reset(){
           this.address="",
             this.time="",
            this.register = '',
            this.cname = "",
            this.caddress = "",
            this.contway = '',
            this.fax = "",
            this.email = "",
            this.postcode = "",
            this.ecotype = "",
            this.legPersonCode = "",
            this.legPerson = '',
            this.legPersonIdcode = '',
            this.defPrincipal = "",
            this.defDepartContway = "",
            this.businessScope = "",
            this.registeredCapital = "",
            this.floorSpace = '',
            this.agency = '',
            this.agencyName = '',
            this.specRecordCode = '',
            this.specRecordAgency = "",
            this.specRecordAname = '',
            this.induLicenceCode = '',
            this.induLicenceAgency = '',
            this.unisocialCreditCode = '',
            this.unisocialLicenceAgency = '',
            this.busLicenceCode = "",
            this.busLicenceAgency = '',
            this.taxLicenceCode = '',
            this.taxLicenceAgency = ''
        },
        phone(){
             if(!this.contway.match(/^[1][3,4,5,6,7,8,9][0-9]{9}$/)){
                  this.p=true
             }else{
               this.p=false  
             }
        },
        idcard(){
             if(!this.legPersonIdcode.match(/^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/
)){
                  this.c=true
             }else{
               this.c=false  
             }
        },
        cfox(){
             if(!this.email.match(/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/)){
                  this.f=true
             }else{
               this.f=false  
             }
        },
        login(){
            this.$router.push('/login')
        },
       getRegister(){
              axios({
          method: 'post',
          url: process.env.API_HOST+ '/qy/getRegister',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
        }).then(res=>{
            console.log(res)
                this.registerlist=res.data.data;
        })
        },
          getEcotype() {
           axios({
          method: 'post',
          url: process.env.API_HOST+ '/qy/getEcotype',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
        }).then((res) => {
          console.log(res)
          this.ecotypelist = res.data.data;
        })
      }
    },
}
</script>
<style >
   .register{
  position: fixed;
  width: 100%;
  height: 100%;
  background: #F3F5FE;
  overflow: scroll;
  }
  .register  .contain{
      width: 95%;
      border :1px solid #ccc;
      margin: 20px auto;
      border-radius:5px;
      background: #fff; 
      margin-top:100px;
      }
     .register table tr td #time input.el-input__inner{
      border:0  !important;
      width: 200px;
}   
</style>


