<template>
    <div class="accident">
        <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span>事故车辆附加登记</span>    
        </div></el-col>
       </el-row>
       <div style="margin-left:30px;">
           <el-row :gutter="10">
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记时间：</td>
                   <td>
                    <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:30px;" id="time"> 
                                <el-date-picker
                                   v-model="time"
                                   type="date"
                                   value-format="yyyyMMdd"
                                   placeholder="选择日期"  size="mini" style="width:150px;" @blur="receive">
                               </el-date-picker>
                        </div>    
                    </td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记序号：</td>
                    <td><select name="" id="" v-model="receiveId"><option value=""  disabled>请选择</option>
                       <option :value="item" v-for="item of list" :key="item.id">{{item.receiveId}}</option></select></td>  
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>事故证明编号：</td>
                   <td><input type="text" placeholder="请输入" v-model="accidentCode"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>事故证明出具单位名称：</td>
                   <td><input type="text" placeholder="请输入" v-model="accidentUnit"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>损毁部位1：</td>
                   <td><input type="text" placeholder="请输入" v-model="damage1"><i class="el-icon-circle-plus" style="color:#BADA46;font-size:25px;" @click="addinput"></i></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" v-for='(ite,index) in items' :key="ite.id"><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td>损毁部位{{index+2}}：</td>
                   <td><input type="text" placeholder="请输入" v-model="items[index]"><i class="el-icon-remove" style="color:#BADA46;font-size:25px; cursor: pointer;vertical-align: middle;" @click="sub(index)"></i></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24"  > <div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>报告人：</td>
                   <td><input type="text" placeholder="请输入" v-model="reporter"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                 <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red"></span>备注：</td>
                   <td><el-input
                        type="textarea"
                        autosize
                        placeholder="请对车身附着物等进行简要概述"
                        v-model="textarea1">
                        </el-input></td>     
                 </tr>     
                </table>    
                </div></el-col>
                </el-row>
       </div>
       <div >
           <el-row>
                <el-button type="primary" @click="define">确定</el-button>
                <el-button type="info"  @click="reset">取消</el-button>
            </el-row>
       </div>
    </div>
</template>
<script>
import axios from "axios"
export default {
    data() {
        return {
            reporter:"",
            damage1:"",
            list:"",
            accidentUnit:"",
             accidentCode:"",
             receiveId:"",
              time:"",
              textarea1: '',
              items: [],
            
        }
    },
    methods: {
      reset(){
          this.time=""
                            this.textarea1=""
                                   this.receiveId="",
                                      this.accidentCode="",
                                      this.accidentUnit="",
                               this.reporter="",
                             this.damage1="",
                             this.items=""
      },
        receive(){
              axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url:process.env.API_HOST+  '/api/qy/getReceiveId', 
                          data:JSON.stringify({
                           startTime:this.time,
                           })
                  }).then((res)=>{
                      console.log(res)
                    switch(res.data.code){
                      case 200:
                      this.list=res.data.data
                      
                      break;
                      case 505:
                        this.$router.push("/login")
                        break;
                      default:
                        this.$message({
                          message:"错误",
                          type:"error"
                        })  
                    }
                    
                    //  this. receiveId=res.data.data
            })
        },
        define(){
          if(
             this.time==""||
             this.receiveId==""||
             this.accidentCode==""||
              this.accidentUnit==""||
              this.reporter==""||
              this.damage1==""
          ){
              this.$message({
                                 message:'输入信息不完整',
                                  type: 'warning'
                            })
                return
          }else{
               axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                     token:JSON.parse(localStorage.getItem('data')).token
                           },
                        
                          url:process.env.API_HOST+ '/qy/RegistrationAccident', 
                           data:JSON.stringify({
                           receiveId:this.receiveId,
                           accidentCode:this.accidentCode,
                           accidentUnit:this.accidentUnit,
                           reporter:this.reporter,
                           damage1:this.damage1,
                           damage2:this.items[0],
                           damage3:this.items[1]
                           })
                  }).then((res)=>{
                      let that=this
                      switch(res.data.code){
                           case 200:
                            that.$message({
                                 message:'提交成功',
                                  type: 'success'
                            })
                            this.time=""
                            this.textarea1=""
                                   this.receiveId="",
                                      this.accidentCode="",
                                      this.accidentUnit="",
                               this.reporter="",
                             this.damage1="",
                             this.items=""
                             
                            break;
                            case 404:
                                 that.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                            break;
                            case 505:
                                 that.$message({
                                 message:'token失效重新登录',
                                  type: 'warning'
                            })
                            that.$router.push("/login")
                            break;

                      }
                   })
                 .catch((err)=>{
                   console.log(err)
                      this.$message({
                                 message:'提交失败',
                                  type: 'error'
                            })
                })
          }
             
        },
        addinput(){
            if(this.items.length<2){
                 this.items.push("")
            }else{
                return
            }
          },
          sub(index){
            this.items.splice(index, 1);
          }
    },
}
</script>
<style lang="css" >
  .accident  .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   padding: 0 0 20px;
   font-size: 24px;
  }
  .accident  .grid-content{
      margin: 10px 0;
      margin-left: 30px;
  }
  .accident  table tr td:nth-child(1){
     width: 200px;
     /* border: 1px solid red; */
     text-align: left;
  }
  .accident  select{
      width:304px;
      height: 32px;
  }
  .accident  input{
      width: 300px;
      height: 28px;
  }
 .accident  .el-row{
     text-align: left;
     margin-left: 10%;
     margin-top: 10px;
 }
 .accident  table tr td #time input.el-input__inner{
    border:0  !important;
} 
.accident  textarea.el-textarea__inner{
     border-radius:0  !important;
     border:1px solid  #aaa;
     width:304px;
     height: 100px !important;;
}
.accident .el-icon-circle-plus,.accident .el-icon-remove-outline{
    vertical-align: middle;
    cursor: pointer;
}
</style>



