<template>
    <div class="mt">
       <button  @click="dialogFormVisible = true" ><p style="margin-top:10px;">车辆美容</p>
                       <p >＋</p> </button>

<el-dialog title="车辆美容" :visible.sync="dialogFormVisible" style="text-align:left">
 <el-row>
    
             <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                <td><span style="color:red">*</span>登记时间：</td>
                   <td>
                     <div style="width:300px;height:32px;border:1px solid #999;background:#FFF;line-height:32px;"
                    id="time">
                    <el-date-picker v-model="time" type="date" value-format="yyyyMMdd" placeholder="选择日期" size="mini"
                      style="width:150px;" @blur="receive">
                    </el-date-picker>
                  </div>
                     </td>     
                 </tr>     
                </table>    
                </div></el-col>     
     <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>登记序号：</td>
                   <td><select name="" id="" v-model="receiveId">
                     <option value="" disabled>请选择</option>
                       <option :value="item" v-for="item of list" :key="item.id">{{item.receiveId}}</option>
                     </select></td>     
                 </tr>     
                </table>    
                </div></el-col>
     <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>项目序号：</td>
                   <td><input type="text" placeholder="请输入" maxlength="2" v-model="progectId"></td>     
                 </tr>     
                </table>    
                </div></el-col>
                <el-col  :md="24" ><div class="grid-content bg-purple">
                 <table>
                  <tr>
                   <td><span style="color:red">*</span>美容项目：</td>
                   <td><input type="text" placeholder="请输入" v-model="proBeautify"></td>     
                 </tr>     
                </table>    
                </div></el-col>
              
 </el-row>
   <div slot="footer" class="dialog-footer" style="text-align:center">
      <el-button type="primary" @click="define">确 定</el-button>
    <el-button @click="reset">取 消</el-button>
    
  </div>
</el-dialog>
    </div>
</template>
<script>
 import axios from "axios"
export default {
    data() {
      return {
        time:'',
        list:'',
        receiveId:'',
        progectId:'',
        proBeautify:'',
        textarea1:"",
        dialogTableVisible: false,
        dialogFormVisible: false,
        
        formLabelWidth: '120px'
      }
    },
    methods: {
      reset(){
         this.time='',
        this.receiveId='',
        this.progectId='',
        this.proBeautify=''
        this.dialogFormVisible = false
      },
      define(){
        if(
          this.time==''||
        this.receiveId==''||
        this.progectId==''||
        this.proBeautify==''
        ){
              this.$message({
                                 message:'输入信息不完整',
                                  type: 'warning'
                            })
                return
        }else{
          axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/RegistrationBeauty', 
                          data:JSON.stringify({
                           receiveId:this.receiveId,
                            progectId:this.progectId,
                            proBeautify:this.proBeautify
                           })
                  }).then((res)=>{

                    console.log(res)
                     switch(res.data.code){
                       case 200:
                        this.$message({
                          message:"提交成功",
                          type:"success"
                        })
                        var that=this 
                        setTimeout(()=>{
                         that.dialogFormVisible=false,
                         that.receiveId="",
                         that.progectId="",
                          that.proBeautify=""
                          that.time=""
                        },150)
                        break;
                        case 505:
                          that.$router.push("/login") 
                          break;

                          default:
                            that.$message({
                          message:"提交失败",
                          type:"error"
                        }) 

                     }
            })
        }
              
      },
      receive(){
          axios({
                    method: 'post',
                    headers: {
                        'Content-Type':'application/json;charset=UTF-8',
                         token:JSON.parse(localStorage.getItem('data')).token
                           },
                          url: process.env.API_HOST+'/qy/getReceiveId', 
                          data:JSON.stringify({
                           startTime:this.time,
                           })
                  }).then((res)=>{
                    switch(res.data.code){
                      case 200:
                      this.list=res.data.data
                      
                      break;
                      case 505:
                        this.$router.push("/login")
                        break;
                      default:
                        this.$message({
                          message:"错误",
                          type:"error"
                        })  
                    }
                    
                    //  this. receiveId=res.data.data
            })
      }

    },
}
</script>
<style scope>
 .mt select{
      width:304px;
      height: 32px;
  } 
  .mt input{
      width: 300px;
      height: 28px;
  }  
 .mt table{
      margin: 0 auto;
  }
  .mt table tr td:nth-child(1){
      width: 100px;
      text-align: left;
  }
  .mt textarea.el-textarea__inner{
     border-radius:0  !important;
     border:1px solid  #aaa;
     width:304px;
     height: 100px !important;;
}
.mt>button{
   background:#4C72A7; width: 300px;border:1px solid #4C72A7;border-radius:10px;outline:none;cursor: pointer;color: #fff;font-size: 24px;
}
.mt>button p{
    margin: 0;
}
 .mt table tr td #time input.el-input__inner{
    border:0  !important;
} 

</style>


